.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.ing-fluid {
  width: 100%;
}

.text-center {
  text-align: center;
}

.contenido-centrado {
  max-width: 70rem;
  margin: 0 auto;
}

.ver-contraseña {
  cursor: pointer;
  color: #333333;
  bottom: 1.2rem;
  right: 0;
  position: absolute;
  padding: 0.2rem 1rem;
}

.m-r-2 {
  margin-right: 1rem;
}

.modo__oscuro {
  background-color: #1a1a1a;
}
.modo__oscuro p {
  color: #FFFFFF;
}
.modo__oscuro blockquote, .modo__oscuro h1, .modo__oscuro h2, .modo__oscuro h3 {
  color: #e1e1e1;
}
.modo__oscuro .card {
  background-color: #333333;
  border: 2px solid #333333;
}
.modo__oscuro .card__precio {
  color: #71B100;
}
.modo__oscuro .card__icono {
  filter: invert(100%);
}
.modo__oscuro .card__cantidad {
  filter: invert(80%);
}
.modo__oscuro .form__label {
  color: #e1e1e1;
}
.modo__oscuro .form__input, .modo__oscuro .form__select {
  color: #e1e1e1;
  background-color: #333333;
  border: none;
}
.modo__oscuro .tabla {
  color: #FFFFFF;
  border: 1px solid #000000;
}
.modo__oscuro .tabla__titulo {
  border: 1px #000000 solid;
}
.modo__oscuro .tabla__valor--id {
  background-color: #E08709;
  color: #000000;
  opacity: 0.8;
}
.modo__oscuro .tabla__valor {
  border: none;
  border-bottom: 1px #000000 solid;
}
.modo__oscuro .ver-contraseña {
  color: #FFFFFF;
}

.alerta {
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  padding: 0.5rem 0;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
}
.alerta.error {
  background-color: #ac0c0c;
}
.alerta.exito {
  background-color: #71B100;
}

.cambios {
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  padding: 0.5rem 0;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
}
.cambios.correcto {
  background-color: #71B100;
}
.cambios.actualizado {
  background-color: #E08709;
}
.cambios.eliminado {
  background-color: #ac0c0c;
}

.tabla {
  margin-top: 4rem;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px #e1e1e1 solid;
  text-align: center;
}
.tabla__thead {
  background-color: #71B100;
  opacity: 0.7;
}
.tabla__titulo {
  border-left: 1px #e1e1e1 solid;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .tabla__titulo {
    font-size: 2rem;
  }
}
.tabla__valor {
  border-bottom: 1px solid #e1e1e1;
  padding: 2rem 1rem;
}
@media (min-width: 768px) {
  .tabla__valor {
    font-size: 2rem;
  }
}
.tabla__valor--id {
  color: #71B100;
  font-weight: bold;
}
.tabla__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .tabla__img {
    max-width: 20rem;
  }
}

.form__btn {
  margin-bottom: 1rem;
}
.form__btn.btn__amarillo {
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
  font-size: 1.7rem;
}
.form__btn.btn__amarillo:hover {
  color: #FFFFFF;
  background-color: #c77808;
}

.btn__rojo {
  width: 100%;
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #ac0c0c;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
  font-size: 1.7rem;
}
.btn__rojo:hover {
  color: #FFFFFF;
  background-color: #940a0a;
}

.img__small {
  max-width: 30rem;
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding-top: 5rem;
  padding-bottom: 2rem;
}
.header.inicio {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  border: 1.5px solid #FFFFFF;
  border-radius: 5px;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}
.header__modo {
  filter: invert(100%);
  cursor: pointer;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .header__modo {
    justify-content: end;
  }
}
.header__dark {
  display: block;
  width: 3rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header__dark {
    margin-bottom: 0;
  }
}
.header__nav {
  display: none;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__nav {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
  }
}
.header__link {
  color: #FFFFFF;
  font-size: 1.7rem;
}
.header__link:hover {
  color: #71B100;
  background-color: transparent;
}
.header__h1 {
  font-size: 3.5rem;
  margin-top: 5rem;
  color: #FFFFFF;
  font-weight: bold;
}
@media (min-width: 768px) {
  .header__h1 {
    font-size: 4rem;
    text-align: left;
    margin-top: 25rem;
    width: 65%;
  }
}

.header__hamburguesa {
  cursor: pointer;
  width: 4rem;
  height: 6.5rem;
}
@media (min-width: 768px) {
  .header__hamburguesa {
    display: none;
  }
}

.mostrar {
  display: flex;
  opacity: 1;
}

.footer {
  margin-top: 5rem;
  background-color: #333333;
  padding-block: 1.5rem;
}
@media (min-width: 768px) {
  .footer__contenido {
    display: flex;
    justify-content: space-between;
  }
}
.footer__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__nav {
    justify-content: center;
    gap: 2rem;
    flex-direction: row;
  }
}
.footer__link:hover {
  color: #71B100;
  background-color: transparent;
}
.footer__copy {
  color: #FFFFFF;
  text-align: center;
}

.nosotros {
  padding-block: 5rem;
}
.nosotros__title {
  color: #333333;
  font-weight: bold;
}
@media (min-width: 768px) {
  .nosotros__iconos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}

.icono__imagen {
  height: 10rem;
}
.icono__title {
  font-size: 2.3rem;
  text-align: center;
  font-weight: bold;
}
.icono__info {
  text-align: justify;
}

.anuncios {
  padding-top: 4rem;
}
@media (min-width: 768px) {
  .anuncios__cards {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .anuncios__cards {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}
.anuncios__dbtn {
  display: flex;
  justify-content: flex-end;
}
.anuncios__btn {
  margin-block: 2.5rem;
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #71B100;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
}
.anuncios__btn:hover {
  color: #FFFFFF;
  background-color: #619800;
}

.card {
  background-color: #e1e1e1;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .card {
    margin-top: 0;
  }
}
.card__info {
  padding: 2rem;
}
.card__title {
  margin-top: 0;
  text-align: center;
}
.card__precio {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
  margin-block: 1rem;
}
.card__iconos {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
  gap: 1rem;
}
@media (min-width: 768px) {
  .card__iconos {
    justify-content: space-between;
    gap: 3rem;
  }
}
.card__icono {
  display: flex;
  gap: 1rem;
}
.card__btn {
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
}
.card__btn:hover {
  color: #FFFFFF;
  background-color: #c77808;
}

.busca {
  background-image: url("../img/encuentra.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 15rem 0;
}
.busca__contenido {
  text-align: center;
}
.busca__title {
  font-weight: bold;
  color: #FFFFFF;
}
.busca__p {
  text-align: center;
  color: #FFFFFF;
}
.busca__btn {
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
  display: inline;
}
.busca__btn:hover {
  color: #FFFFFF;
  background-color: #c77808;
}

.blogTestimonios {
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .blogTestimonios {
    display: grid;
    grid-template-columns: 60% 38%;
    gap: 2rem;
  }
}
.blog {
  margin-bottom: 2rem;
}
.blog__info {
  padding: 2rem;
}
.blog__title {
  color: #333333;
  margin-bottom: 0;
  display: inline-block;
  font-weight: bold;
}
.blog__title::after {
  content: "";
  display: block;
  height: 0.5rem;
  background-color: #507e00;
  border-radius: 50px 0;
}
.blog__autor {
  font-weight: bold;
}
.blog span {
  color: #E08709;
}

.testimonio {
  position: relative;
  border-radius: 10px;
  border: 2px #e1e1e1 solid;
  background-color: #71B100;
  padding: 2.5rem;
  padding-left: 5rem;
  margin-bottom: 1.5rem;
}
.testimonio::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  display: block;
  height: 3rem;
  width: 4rem;
  left: 1rem;
  top: 2.8rem;
  background-repeat: no-repeat;
}
.testimonio__p {
  margin-top: 0;
  color: #FFFFFF;
}
.testimonio__author {
  margin-bottom: 0;
  font-size: 1.5rem;
  text-align: end;
  color: #333333;
  font-weight: bold;
}

.conocenos {
  padding-block: 5rem;
}
.conocenos__info {
  margin-top: 2rem;
}
@media (min-width: 1024px) {
  .conocenos__info {
    margin-top: 0;
  }
}
.conocenos__year {
  margin: 0;
  font-weight: bold;
  color: #333333;
  font-size: 2.3rem;
}
.conocenos__year::before {
  content: " ";
  border-right: 2.8px solid #71B100;
  margin-right: 0.5rem;
}
.conocenos__title {
  font-weight: bold;
}
@media (min-width: 1024px) {
  .conocenos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

@media (min-width: 768px) {
  .blog__pagina {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
  }
}

.form {
  margin-bottom: 5rem;
  margin-top: 3rem;
}
.form__title {
  font-weight: bold;
}
.form__fieldset {
  margin-bottom: 2rem;
}
.form__legend {
  color: #333333;
  background-color: #e1e1e1;
  padding: 0.5rem 2rem;
  font-size: 1.7rem;
  text-transform: uppercase;
  font-weight: bold;
}
.form__campo {
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .form__flex {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;
  }
}
.form__flex--direction {
  text-align: center;
  padding-bottom: 3rem;
}
@media (min-width: 480px) {
  .form__flex--direction {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .form__flex--direction {
    padding-bottom: 0;
  }
}
.form__label {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.6rem;
}
.form__input, .form__select {
  border-radius: 7px;
  border: #e1e1e1 1px solid;
  padding: 1rem;
  width: 100%;
}
.form__radio {
  padding-right: 1rem;
}
.form__p {
  text-align: center;
  color: #333333;
  font-weight: bold;
  padding: 1rem;
  margin: 0;
}
.form__btn {
  display: inline;
  cursor: pointer;
  font-size: 2rem;
  border: none;
  border-radius: 3px;
  background-color: #71B100;
  color: #FFFFFF;
  display: block;
  padding: 0.7rem 2rem;
  text-align: center;
  font-weight: bold;
}
.form__btn:hover {
  color: #FFFFFF;
  background-color: #5a8d00;
}
.form__btn--amarillo {
  background-color: #E08709;
}
.form__campo--position {
  position: relative;
}

.btn {
  display: flex;
}

.btn__flex {
  display: flex;
  justify-content: space-between;
}

.anuncio__title {
  text-transform: uppercase;
}
.anuncio__img {
  max-width: 100rem;
