@use 'base/mixins' as m;
@use 'base/variables' as v;

.nosotros {
  padding-block: 5rem;

  &__title {
    color: v.$grisOscuro;
    font-weight: bold;
  }


  &__iconos {

    @include m.tablet {
      @include m.grid(3, 3rem);
    }
  
  }

 
}

.icono {
  
  &__imagen {
    height: 10rem;
  }

  &__title {
    font-size: 2.3rem;
    text-align: center;
    font-weight: bold;
  }

  &__info {
    text-align: justify;
  }
}