@use 'base/variables' as v;
@use 'base/mixins' as m;


.conocenos{
  padding-block: 5rem;

  &__info {
    margin-top: 2rem;

    @include m.desktop {
      margin-top: 0
    }
  }

  &__year {
    margin: 0;
    font-weight: bold;
    color: v.$grisOscuro;
    font-size: 2.3rem;


    &::before {
      content: " ";
      border-right:2.8px solid v.$verde ;
      margin-right: .5rem;
    }

  }

  &__title {
    font-weight: bold
  }


  &__grid {
    @include m.desktop {
      @include m.grid(2, 2rem);
    }
  }
}
