@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonios {


  &__title {
   
  }
}


.testimonio {
  position: relative;
  border-radius: 10px;
  border: 2px v.$gris solid;
  background-color: v.$verde;
  padding: 2.5rem ;
  padding-left: 5rem;
  margin-bottom: 1.5rem;

  &::before {
    position: absolute;
    content: "";
    background-image: url(../img/comilla.svg);
    display: block;
    height: 3rem;
    width: 4rem;
    left: 1rem;
    top:2.8rem;
    background-repeat: no-repeat;
  }

 

  &__p {
    margin-top: 0;
    color: v.$blanco;
  
  }

  &__author {
    margin-bottom: 0;
    font-size: 1.5rem;
    text-align: end;
    color:v.$grisOscuro;
    font-weight: bold;
  }
}