@use 'base/mixins' as m;
@use 'base/variables' as v;

.busca{
  background-image:url('../img/encuentra.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 15rem 0;;

  &__contenido {
    text-align: center;

  }

  &__title {
    font-weight: bold;
    color: v.$blanco;
  }

  &__p {
    text-align: center;
    color: v.$blanco;
  }

  &__btn {
    @include m.btn(v.$amarillo, v.$blanco);
    @include m.hover(darken(v.$amarillo, 5%), v.$blanco);
    display: inline;
  }
}