@use 'base/mixins' as m;
@use 'base/variables' as v;


.anuncios {
  padding-top: 4rem;

  &__cards {
    
    @include m.tablet {
      @include m.grid(2,1.5rem);
    }
    @include m.desktop {
      @include m.grid(3, 2rem);
    }
  }

  &__dbtn {
    display: flex;
    justify-content: flex-end;
  }

  &__btn {
    margin-block:2.5rem;
    @include m.btn(v.$verde, v.$blanco);
    @include m.hover(darken(v.$verde, 5%), v.$blanco)
  }
}


.card {
  background-color: v.$gris;
  margin-top: 2rem;
  @include m.tablet {
    margin-top:0;
  }

  &__info {
    padding: 2rem;
  }

  &__title {
    margin-top: 0;
    text-align: center;

  }
  &__precio {
    color: v.$verde;
    font-weight: bold;
    font-size: 3rem;
    text-align: center;
    margin-block: 1rem;
  }


  &__iconos {
    list-style-type: none;
    display: flex;
    justify-content: flex-start;
    padding-left: 0;
    gap: 1rem;
    
    @include m.tablet{
      justify-content: space-between;
      gap: 3rem;
    }
  }

  &__icono {
    display: flex;
    gap: 1rem;
  }

  &__btn {
    @include m.btn (v.$amarillo, v.$blanco);
    @include m.hover (darken(v.$amarillo, 5%), v.$blanco);
  }
}