/** Media Queries **/
@use 'variables' as v;

@mixin telefono {
    @media (min-width: #{v.$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{v.$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{v.$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
    align-items: center;
}

@mixin hover($colorF, $colorT){

    &:hover {
        color: $colorT;
        background-color: $colorF;
    }
}
@mixin btn($colorF, $colorT){
    font-size: 2rem;
    border: none;
    border-radius: 3px;
    background-color: $colorF;
    color: $colorT;
    display: block;
    padding: .7rem 2rem;
    text-align: center;
    font-weight: bold;
}