@use 'variables' as v;


.modo__oscuro {
  background-color: darken(v.$grisOscuro, 10%);

  p{
    color: v.$blanco;
  }

  blockquote, h1,h2,h3{
    color: v.$gris;
  }

  .card{
    background-color: v.$grisOscuro;
    border: 2px solid v.$grisOscuro;

    &__precio {
      color: v.$verde;
    }

    &__icono {
      filter: invert(100%);
    }
    &__cantidad {
      filter: invert(80%);
    }
  }

  .form {

    &__label {
      color: v.$gris;
    }

    &__input, &__select{
      color: v.$gris;
      background-color: v.$grisOscuro;
      border: none;
    }
  
    
  }

  .tabla {
    color: v.$blanco;
    border: 1px solid v.$negro;


    &__titulo{
      border: 1px v.$negro solid;
    }

    &__valor--id {
      background-color: v.$amarillo;
      color: v.$negro;
      opacity: .8;
    }

    &__valor {
      border: none;
      border-bottom: 1px v.$negro solid;
    }

  }
    // mostrar
  .ver-contraseña{
    color: v.$blanco;
  }

}





