@use 'base/mixins' as m;
@use 'base/variables' as v;


.footer {
  margin-top: 5rem;
  background-color: v.$grisOscuro;
  padding-block: 1.5rem;

  &__contenido {
    
    @include m.tablet {
      display: flex;
      justify-content: space-between;
    }
  }
 
  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
      justify-content: center;
      gap: 2rem;
      flex-direction: row;
    }
  }

  &__link {
    @include m.hover(transparent, v.$verde);

  }
  
  &__copy {
    color: v.$blanco;
    text-align: center;
  }
}
