
@use 'base/mixins' as m;
@use 'base/variables' as v;


.form{
  margin-bottom: 5rem;
  margin-top: 3rem;


  &__title {
    font-weight: bold;
  }

  &__fieldset {
    margin-bottom: 2rem;
  }

  &__legend {
    color: v.$grisOscuro;
    background-color: v.$gris;
    padding: .5rem 2rem;
    font-size: 1.7rem;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__campo {
    padding: 1rem 0;
  }



  &__flex{

    @include m.tablet {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      gap:1rem;
    }
  }

  &__flex--direction {
    text-align: center;
    padding-bottom: 3rem;
    @include m.telefono {
      display: flex;
      flex-direction: row;
      justify-content: center;
     
    }

    @include m.tablet {
      padding-bottom: 0;
    }
  
  }

  &__label {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.6rem;
  }
 

  &__input, &__select{
    border-radius: 7px;
    border: v.$gris 1px solid;
    padding: 1rem ;
    width: 100%;
  }
  

  &__radio {
    padding-right: 1rem;
  }

  &__p {
    text-align: center;
    color: v.$grisOscuro;
    font-weight: bold;
    padding: 1rem;
    margin: 0;
  }

  &__btn {
  
    display: inline;
    cursor: pointer;
    @include m.btn (v.$verde, v.$blanco);
    @include m.hover(darken(v.$verde, 7%), v.$blanco);
  }
  
  &__btn--amarillo {
    background-color: v.$amarillo;
  }

  &__campo--position {
    position: relative;
  }


 
}

.btn {
  display: flex;

}

  .btn__flex {
    display: flex;
    justify-content: space-between;
  }



