
@use 'base/variables' as v;
@use 'base/mixins' as m;


 .no-webp .inicio {
  background-image: url('../img/header.jpg');
}
.webp .inicio {
  background-image: url('../img/header.webp');
}

.header {
  background-color: v.$grisOscuro;
  padding-top: 5rem;
  padding-bottom: 2rem;

  &.inicio {
    background-position: center center;
    background-repeat: no-repeat;
    background-size:cover;
  }
  
  &__barra {
    display: flex;
    flex-direction: column;
    align-items: center;
  

    @include m.tablet {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__logo {
    border: 1.5px solid v.$blanco;
    border-radius: 5px;
   margin-bottom: 4rem;
   @include m.tablet {
    margin-bottom: 0;
   }

  }



  &__modo {
    filter: invert(100%);
    cursor: pointer;
    margin: 0 auto;
    display: flex;
    justify-content: center;

    @include m.tablet {
      justify-content: end;
    }
   
    
  }
  &__dark {
    display: block;
    width: 3rem;
    margin-bottom: 2rem;

    @include m.tablet {
      margin-bottom: 0;
    }
    
  }

  &__nav {
   display:none;

   

    flex-direction: column;
    align-items: center;
    
  

    @include m.tablet {
      display: flex;
      flex-direction: row;
      gap: 1.5rem;
    }
  }

  &__link {
    color: v.$blanco;
    font-size: 1.7rem;

    @include m.hover(transparent, v.$verde);
  }

  &__h1 {
    font-size: 3.5rem;
    margin-top: 5rem;
    color: v.$blanco;
    font-weight: bold;

    @include m.tablet {
      font-size: 4rem;
      text-align: left;
      margin-top: 25rem;
      width: 65%;
    }
  }

}

.header__hamburguesa {
  cursor: pointer;
  width: 4rem;
  height: 6.5rem;

  @include m.tablet {
    display: none;
  }
}

.mostrar {
  display: flex;
  opacity: 1;
  

}