@use 'base/variables' as v;
@use 'base/mixins' as m;



// alertas
  .alerta {
    margin-top: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    padding: .5rem 0;
    color: v.$blanco;
    text-transform: uppercase;
    font-weight: bold;

    &.error {
      background-color: v.$rojo;
    }
    &.exito {
      background-color: v.$verde;
    }

  }
  
  .cambios {
    margin-top: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    padding: .5rem 0;
    color: v.$blanco;
    text-transform: uppercase;
    font-weight: bold;
    &.correcto {
      background-color: v.$verde;
    }
    &.actualizado {
      background-color: v.$amarillo;
    }
    &.eliminado {
      background-color: v.$rojo;
    }
  }


// tablas

 .tabla {
  margin-top: 4rem;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px v.$gris solid;
  text-align: center;


  &__thead {
    background-color: v.$verde;
    opacity: .7;
   

  }
  
  

  &__titulo {
    border-left: 1px v.$gris solid;
    padding: 1rem ;
    color: v.$blanco;
    font-size: 1.6rem;
    
    @include m.tablet {
      font-size: 2rem;
    }

  }

  &__valor {
    border-bottom: 1px solid v.$gris;
    padding: 2rem 1rem;
    @include m.tablet {
      font-size: 2rem;
    }
 
  }

  &__valor--id {
    color: v.$verde;
    font-weight: bold;
  }



  &__img {
    margin: 0 auto;

    @include m.tablet {
      max-width: 20rem;
    }
  }


 }









  // botones
  
  .form__btn {
    margin-bottom: 1rem;

     
    &.btn__amarillo {
      @include m.btn(v.$amarillo, v.$blanco);
      @include m.hover(darken(v.$amarillo,5%), v.$blanco);
      font-size: 1.7rem;
    }
  }

  .btn__rojo {
    width: 100%;
    @include m.btn(v.$rojo, v.$blanco);
    @include m.hover(darken(v.$rojo,5%), v.$blanco);
   font-size: 1.7rem;
  }


  // imagenes

.img {
  
  &__small {
  max-width: 30rem;
  }
}